.news {
  width: 100%;
  height: 100%;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  .news_card {
    width: 100%;
    max-height: 200px;
    overflow-y: auto;
    border: 0.2rem solid black;
    margin: 0.5rem;
    padding: 1rem;
    line-height: 1.5rem;
    .author {
      font-style: italic;
      font-size: .8rem;
    }
    .description {
      font-size: .8rem;
    }
  }
}